<template>
  <div id="pointE">
    <div id="viewDiv"></div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="right">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="right">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
    <div id="toolsbox" ref="toolsbox" style="padding: 10px;background: #fff;height: 110px">
      <Form ref="edit" :label-width="40" inline>
        <FormItem label="容差">
          <InputNumber :max="10000" :min="1" v-model="rc"></InputNumber>
        </FormItem>
        <Button style="width: 20px;margin-left: -15px;margin-right: 10px" type="text">米</Button>
        <Button @click="gdShp" type="primary" style="margin-right: 8px">生成</Button>
        <Button @click="saveShp" type="primary" style="margin-right: 8px">保存</Button>
        <Button @click="gdFile" type="primary">下载</Button>
      </Form>
      <div style="margin-top: -14px;border-top:1px solid #d1d1d1;padding-top: 10px">
        <Form ref="query" :label-width="40" inline>
          <FormItem label="x">
            <Input v-model="x" style="width: 80px"></Input>
          </FormItem>
          <FormItem label="y">
            <Input v-model="y" style="width: 80px"></Input>
          </FormItem>
          <Button type="primary" @click="queryPoint">搜索</Button>
        </Form>
      </div>
    </div>
  </div>
</template>

<script>
import arcgisPackage from '../../utils/arcgisTools'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLineDp, shengjie, shijie, xianjie } from '@/utils/layers'
import { gdShp, saveShp, gdFile, getGdFile } from '../../api/pointExtract'
import Basemapthumbnail from '../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../assets/map_images/Basemapthumbnail1.png'
import collSign3 from '../../assets/boundaryMarker_images/coll_bm3.png'
import collBm3 from '../../assets/boundaryMarker_images/coll_sign3.png'

const markerSymbol = {
  type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
  url: collSign3,
  height: 16,
  width: 16,
  outline: { // autocasts as new SimpleLineSymbol()
    color: [255, 255, 255],
    width: 1
  }
}
const bmSymbol = {
  type: 'picture-marker', // autocasts as new SimpleMarkerSymbol()
  url: collBm3,
  height: 16,
  width: 16,
  outline: { // autocasts as new SimpleLineSymbol()
    color: [255, 255, 255],
    width: 1
  }
}
export default {
  name: 'OnePiece',
  data () {
    return {
      map: null, // 地图实例
      view: null, // 地图视图实例
      rc: 700,
      type: '',
      x: 117.3554,
      y: 30.9117
    }
  },
  methods: {
    /**
     * @Description: 初始化“一张图”数据中心的地图
     * @author 张淼
     * @date 2019/4/16
     */
    async createMap () {
      // 导入需要的arcgis模块
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen
      let GraphicsLayer = await arcgisPackage.GraphicsLayer
      let Sketch = await arcgisPackage.Sketch

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => {
        return res
      })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => {
        return res
      })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => {
        return res
      })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => {
        return res
      })
      // 红线斑块图层
      this.layerRedLine = await redLineDp('hx').then(res => {
        return res
      })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: {
          longitude: 117.528239,
          latitude: 30.884646,
          spatialReference: {
            wkid: 4490
          }
        },
        // center: config.centerPoint,
        zoom: 5
      })
      this.map.add(this.layerRedLine)
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-left')
      // 添加全屏
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 增加地图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.add(basemapToggle, 'top-right')
      this.view.ui.add(this.$refs.toolsbox, 'bottom-right')
      // 新建点图层
      let graphicsLayer = new GraphicsLayer({
        id: 'pe',
        title: ' ',
        elevationInfo: 'on-the-ground'
      })
      this.map.add(graphicsLayer)
      let sketch = new Sketch({
        layer: graphicsLayer,
        view: this.view
      })
      this.view.ui.add(sketch, 'bottom-right')
    },
    /* 生成 */
    async gdShp () {
      this.$Spin.show()
      let Graphic = await arcgisPackage.Graphic
      this.map.findLayerById('pe').removeAll()
      gdShp({
        data: {
          rc: this.rc.toString()
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          let arr = []
          for (let item of res.data.data) {
            let attr = JSON.parse(item.attributes)
            let geom = JSON.parse(item.geometry)
            let point = {
              type: 'point', // autocasts as new Point()
              longitude: geom.rings.split(',')[0],
              latitude: geom.rings.split(',')[1],
              spatialReference: {
                wkid: 4490
              }
            }
            let pointGraphic = new Graphic({
              geometry: point,
              attributes: attr,
              symbol: markerSymbol
            })
            arr.push(pointGraphic)
          }
          this.map.findLayerById('pe').addMany(arr)
          this.$Spin.hide()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /* 保存 */
    saveShp () {
      this.$Spin.show()
      let arr = this.map.findLayerById('pe').graphics.items
      console.log(arr)
      let dataArr = []
      for (let item of arr) {
        let geometry = {
          rings: [[[item.geometry.x, item.geometry.y]]].join(','),
          // x: item.geometry.x,
          // y: item.geometry.y,
          type: 'point'
        }
        let obj = {
          attributes: JSON.stringify(item.attributes),
          geometry: JSON.stringify(geometry)
        }
        dataArr.push(obj)
      }
      saveShp({
        data: {
          data: dataArr
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '保存成功！' })
          this.$Spin.hide()
        } else {
          this.$Notice.error({ title: res.data.msg })
          this.$Spin.hide()
        }
      })
    },
    /* 下载 */
    gdFile () {
      gdFile({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          let link = document.createElement('a')
          link.style.display = 'none'
          let url = res.data.data
          link.href = res.data.data
          link.setAttribute('download', url.split('/')[url.split('/').length - 1])
          document.body.appendChild(link)
          link.click()
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /* 搜索 */
    queryPoint () {
      if (this.x === '' && this.y === '') {
        this.$Notice.error({ title: '请输入坐标' })
      } else {
        let arr = this.map.findLayerById('pe').graphics.items
        let tf = false
        for (let item of arr) {
          // item.symbol = markerSymbol
          if (item.geometry.x === this.x && item.geometry.y === this.y) {
            this.view.goTo(item)
            this.view.zoom = 10
            console.log(item)
            item.symbol = bmSymbol
            tf = true
          }
        }
        if (tf) {
          this.$Notice.error({ title: '坐标不在生态保护红线边界范围内' })
        }
      }
    },
    /* 获取最新点 */
    async getGdFile () {
      let Graphic = await arcgisPackage.Graphic
      this.$Spin.show()
      getGdFile({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          if (res.data.data) {
            let arr = []
            for (let item of res.data.data) {
              let obj = JSON.parse(item)
              let attr = JSON.parse(obj.attributes)
              let geom = JSON.parse(obj.geometry)
              let point = {
                type: 'point', // autocasts as new Point()
                longitude: geom.rings.split(',')[0],
                latitude: geom.rings.split(',')[1],
                spatialReference: {
                  wkid: 4490
                }
              }
              let pointGraphic = new Graphic({
                geometry: point,
                attributes: attr,
                symbol: markerSymbol
              })
              arr.push(pointGraphic)
            }
            this.map.findLayerById('pe').addMany(arr)
            this.$Spin.hide()
          } else {
            this.gdShp()
          }
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      })
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        default:
          break
      }
    }
  },
  async mounted () {
    // 初始化地图
    await this.createMap()
    await this.getGdFile()
  }
}
</script>

<style lang="scss" scoped>
  #pointE{
    height: 100%;
    display: flex;
    position: relative;
  }
  #layersControl{
    .layersControl_box{
      padding:3px;
      background: #fff;
      border: 1px solid #d1d1d1;
      .fraggableBox{
        padding: 0;
        .fraggableLi{
          min-width: 100px;
          padding: 5px 8px;
          border-top: 1px solid #d1d1d1;
        }
      }
    }
  }
  #viewDiv{
    height: 100%;
    flex-grow: 2;
  }
  #maptab{
    width: 300px;
    .firstmenu{
      >li{
        margin-bottom: 6px;
        text-align: left;
        >ul{
          >li{
            padding-left: 20px;
          }
        }
        >p{
          line-height: 40px;
          text-align: left;
          font-size: 16px;
          background-color: rgb(42,177,245);
          color: white;
          padding-left: 10px;
          border-radius: 4px;
          cursor: pointer;
          i{
            font-size: 16px;
            line-height: 40px;
            margin-right: 10px;
          }
        }
        .secondmenu{
          label{
            line-height: 30px;
            font-size: 14px;
          }
        }
      }
    }
  }
  #basemapToggle{
    border: 5px #000 solid;
  }
</style>
<style scoped>
  #maptab >>> .ivu-tabs-content{
    height: calc(100% - 40px);
    margin-top: -16px;
  }
  #maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
    padding: 16px;
    border: 1px solid #dcdee2;
    border-radius: 0 0 4px 4px;
    border-top: none;
  }
  #maptab >>> .ivu-tabs-nav{
    display: flex;
    float: none;
  }
  #maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
    flex-grow: 2;
    margin-right: 0;
  }
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
<style lang="scss">
  h3.esri-widget__heading{
    display: none;
  }
  .esri-legend__layer-caption{
    display: none;
  }
  .esri-ui-corner .esri-component.esri-widget--panel{
    width: 200px;
  }
  #pointE{
    .esri-icon-pan,.esri-sketch__tool-section:nth-child(2),.esri-sketch__tool-section:nth-child(3),.esri-icon-map-pin,.esri-icon-polyline,.esri-icon-polygon,.esri-icon-checkbox-unchecked,.esri-icon-radio-unchecked,.esri-icon-undo,.esri-icon-redo{
      display:none
    }
  }
</style>
